
.sie-alert {
  width: 100%;
  padding: 5px 20px;
  font-size: 14px;
}
.sie-alert_success {
  color: #ffffff;
  background-color: #00cc99;
}
.sie-alert_err {
  color: #ffffff;
  background-color: #de3939;
}
.sie-alert_warning {
  color: #e18e0a;
  background-color: #ffe3b6;
}



