
.m-padheader {
  height: 80px;
  width: 100%;
  background-color: #6E5428;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.m-padheader .backbox {
  height: 80px;
  font-size: 36px;
  font-weight: 500;
  line-height: 80px;
  color: white;
  padding-left: 20px;
}
.m-padheader .btnbox {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
}
.m-padheader .btnbox .btn {
  font-family: 微软雅黑;
  font-weight: 400;
  font-style: normal;
  font-size: 20px;
  color: #ffffff;
  width: 91px;
  height: 41px;
  line-height: 41px;
  border: 1px solid white;
  border-radius: 3px;
  text-align: center;
  margin: 0 10px;
}
.m-padheader .btnbox .btn .iconfont {
  font-size: 24px;
}
.m-padheader .iconfont {
  font-size: 30px;
}



