select {
  margin: 0;
  padding: 0;
  font-weight: 300;
  text-indent: 0;
}
a,
button,
textarea,
select {
  background: none;
  -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
  outline: none;
  -webkit-appearance: none;
}
input,
textarea {
  outline: medium;
  border: none;
  border-radius: 0;
}
