
.m-header {
  overflow-y: auto;
  min-height: 50px;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 50px;
  width: 100%;
  background: #22292C;
  color: white;
}
.m-header .logo {
  position: absolute;
  left: 0;
  width: 25%;
  height: 50px;
  text-align: center;
}
.m-header .logo .logotitle {
  display: block;
  font-size: 20px;
  font-weight: 50;
}
.m-header .logo .logodesc {
  display: block;
  line-height: 10px;
  font-size: 10px;
}
.m-header .title {
  font-size: 22px;
  font-weight: 500;
}
.m-header .back {
  position: absolute;
  right: 0;
  top: 12px;
  padding-right: 10px;
  font-size: 18px;
  font-weight: 500;
}



